.flex-container {
  display: flex;
  flex-wrap: wrap; /* アイテムを折り返す設定 */
  justify-content: center; /* アイテムを中央に揃える */
  gap: 30px; /* ボックス間のスペース */
  max-width: 1000px; /* コンテナ全体の幅を調整 */
  margin: 0 auto; /* コンテナをページ中央に配置 */
}

@media (max-width: 600px) {
  .flex-container{
    gap: 10px;
  }
}

.flex-item {
  background-color: rgb(1, 25, 92); /* 背景色 */
  color: white; /* 文字色 */
  padding: 15px;
  text-align: left;
  width: calc(40% - 30px); /* 2列にするための計算 */
  box-sizing: border-box;
  height: 250px; /* ボックスの高さを固定 */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 600px) {
  .flex-item{
    width: calc(50% - 30px);
    padding: 5px;
    height: 150px;
  }
}

.flex-item h2 {
  font-size: 18px;
  margin-top: 0px;
  margin-bottom: 5px;
}

.flex-item .summary {
  font-size: 14px;
}

@media (max-width: 600px){
  .flex-item .summary {
    font-size: 12px;
    line-height: 1.5;
  }  
}

.flex-item .learn {
  margin-top: auto;  /* 縦方向でボックスの一番下に配置 */
  align-self: flex-end;  /* 横方向で右端に揃える */
  color: white !important;
  text-decoration: none;
  font-size: 14px;
}

.learn a {
  color: white !important;  /* リンクの文字色を強制的に白にする */
  text-decoration: none;    /* 下線を削除 */
  font-size: 14px;
}

.learn a:hover {
  color: #cccccc;  /* ホバー時の文字色を変更 */
}

